@font-face {
    font-family: 'SourceHanSansCN Medium';
    src: url('./fonts/SourceHanSansCN-Medium.otf');
}

@font-face {
    font-family: 'SourceHanSansCN ExtraLight';
    src: url('./fonts/SourceHanSansCN-ExtraLight.otf');
}

@font-face {
    font-family: 'digifacewide';
    src: url('./fonts/digifacewide.ttf');
}

body {
    width: 2944px;
    height: 2104px;
    background: url("images/bg.png") no-repeat center / 100% 100%;
}

.header {
    width: 100%;
    height: 95px;
    background: url("images/header-bg.png");
    text-align: center;
    .logo {
        width: 532px;
        font-size: 48px;
        line-height: 90px;
        color: #ffffff;
        display: inline-block;
        height: 78px;
        background: url('images/logo.png') no-repeat left bottom / auto 80%, url('images/logo-name.png') no-repeat 87% 74% / auto 63%;
        font-weight: bolder;
        text-decoration: none;
        letter-spacing: 10px;
    }
}

.anti-terrorism {
    width: 386px;
    height: 90%;
    padding-left: 50px;
    display: inline-block;
    vertical-align: top;
    .at-theday {
        position: relative;
        background: url("images/cont-bg.png");
        background-size: 100% 100%;
        .at-theday-chart {
            padding-left: 15px;
        }
    }
    .key-person {
        font-size: 18px;
        color: #fff;
        width: 85px;
        line-height: 24px;
        position: absolute;
        top: 243px;
        left: 153px;
        text-align: center;
    }
    .value-text {
        font-family: digifacewide;
    }
}

.map-wrap {
    position: relative;
    width: 1055px;
    height: 90%;
     display: inline-block;
    vertical-align: top;
}
#map,
.map-bg {
   
    position: absolute;
    svg {
        margin-top: 150px;
    }
}

.map-bg {
     width: 1055px;
    height: 90%;
    background: url(images/china-map2.png) no-repeat;
    background-size: 98% 105%;
    background-position: 0px 32px;
}

.maintain-stability {
    width: 386px;
    height: 90%;
    display: inline-block;
    vertical-align: top;
}

.area .item-cont {
    height: 90%;
}

.group .item-cont {
    height: 68%;
}

.side {
    .cont-title {
        width: 261px;
        height: 44px;
        line-height: 44px;
        text-align: center;
        color: #ffffff;
        background: url("images/title-bg.png") no-repeat center;
        font-family: 'SourceHanSansCN Medium';
        font-size: 24px;
        margin: 0 auto 11px;
    }
    .at-info {
        width: 100%;
        height: 79px;
        background: url("images/fk-border.png");
        text-align: center;
        li {
            float: left;
            width: 33.33%;
            height: 79px;
            p:first-child {
                color: #91d9ff;
                font-size: 16px;
                font-family: 'SourceHanSansCN ExtraLight';
                line-height: 40px;
                margin-top: 5px;
            }
            p:last-child {
                color: #18a0ff;
                font-size: 17px;
                font-family: 'digifacewide';
            }
        }
    }
    .at-theday {
        width: 100%;
        height: 420px;
        margin-top: 31px;
        &.come {
            margin-bottom: 25px;
        }
        .at-title {
            font-family: "SourceHanSansCN ExtraLight";
            font-size: 18px;
            color: #ffffff;
            line-height: 32px;
            height: 37px;
            text-indent: 45px;
            background: url("images/cont-bg-title.png") no-repeat center;
            span {
                color: #9ec5ff;
                font-size: 16px;
                float: right;
                margin-right: 12px;
                display: inline-block;
                text-indent: 0;
                text-align: right;
                width: 95px;
            }
        }
        .at-theday-cont {
            width: 100%;
            height: 110px;
            font-family: digifacewide;
            font-size: 60px;
            color: #02d1ff;
            text-align: center;
            line-height: 130px;
            background: url("images/fk-gw-number.png") no-repeat center bottom;
            margin-top: -30px;
        }
        .at-theday-per {
            color: #ffffff;
            text-align: center;
            margin: 5px auto 10px;
            span:first-child {
                text-indent: 8px;
                display: inline-block;
                border-left: 3px solid #19d6ff;
                font-size: 14px;
                color: #ffffff;
                font-family: 'SourceHanSansCN ExtraLight';
            }
            span:last-child {
                display: inline-block;
                font-size: 17px;
                font-family: 'digifacewide';
                color: #19d6ff;
            }
        }
        .at-theday-chart {
            width: 100%;
            height: 200px;
        }
        .str_wrap {
            background: none;
        }
        .item-cont {
            .item {
                position: relative;
                background: url("images/item-bg.png") no-repeat;
                width: 100%;
                height: 31px;
                line-height: 30px;
                margin-top: 24px;
                span,
                ul {
                    display: inline-block;
                    vertical-align: middle;
                }
                &>span:first-child {
                    width: 80px;
                    height: 31px;
                    color: #ffffff;
                    font-size: 16px;
                    display: inline-block;
                    overflow: hidden;
                    text-align: center;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }
                div {
                    position: absolute;
                    top: 0;
                    right: 0;
                    font-size: 14px;
                    display: inline-block;
                    span:nth-child(1) {
                        color: #008aff;
                        font-family: 'digifacewide';
                    }
                    span:nth-child(2) {
                        color: #008aff;
                        font-size: 12px;
                        font-family: 'digifacewide';
                    }
                    span:nth-child(3) {
                        color: #707680;
                        font-family: 'digifacewide';
                    }
                }
                ul {
                    position: relative;
                    width: 162px;
                    height: 6px;
                    li {
                        position: absolute;
                        top: 0;
                        left: 0;
                        height: 100%;
                        margin-left: 5px;
                        &:first-child {
                            width: 100%;
                            background: url("images/item-value-2.png") repeat-x;
                        }
                        &:last-child {
                            width: 0;
                            background: url("images/item-value-1.png") repeat-x;
                        }
                    }
                }
            }
            .item2 {
                background: url("images/item-bg2.png") no-repeat;
                height: 28px;
                line-height: 26px;
                margin-top: 32px;
                &>span:first-child {
                    width: 75px;
                    color: #00ffff;
                    height: 28px;
                }
                ul {
                    height: 4px;
                    li {
                        border-radius: 2px;
                        &:first-child {
                            background: #01458f;
                        }
                        &:last-child {
                            width: 0;
                            background: #4ab0df;
                            &:after {
                                content: '';
                                background: #e2e201;
                                height: 6px;
                                width: 6px;
                                border-radius: 3px;
                                float: right;
                                margin-top: -1px;
                                margin-left: -1px;
                            }
                        }
                    }
                }
                div {
                    font-size: 14px;
                    margin-right: 5px;
                    span:nth-child(1) {
                        color: #4ab0df;
                    }
                    span:nth-child(2) {
                        color: #4ab0df;
                        font-size: 12px;
                    }
                }
            }
        }
    }
}


/*地图动画*/

.outerCircle1,
.outerCircle2 {
    animation: outerCircle1 3s infinite linear;
    -webkit-animation: outerCircle1 3s infinite linear;
    transform-origin: 50% 50%;
}

.light-circle {
    animation: aniScale 1s infinite linear;
    -webkit-animation: aniScale 1s infinite linear;
    transform-origin: 50% 50%;
}

@keyframes outerCircle1 {
    0% {
        transform: scale(0.7);
        opacity: 0.5;
    }
    50% {
        transform: scale(1.2);
        opacity: 1;
    }
    100% {
        transform: scale(0.7);
        opacity: 0.5;
    }
}

@keyframes aniScale {
    0% {
        opacity: 0.9;
        transform: scale(0.9);
    }
    50% {
        opacity: 1;
        transform: scale(1.2);
    }
    100% {
        opacity: 0.9;
        transform: scale(0.9);
    }
}

.enter-line,
.out-line {
    stroke-dasharray: 0;
    -webkit-animation: dash 6s linear forwards;
    animation: dash 6s linear forwards;
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
    -webkit-animation-delay: 0s;
    animation-delay: 0s;
}

.enter-line {
    stroke-dashoffset: -200;
}

@keyframes dash {
    to {
        stroke-dashoffset: 0;
    }
}

.out-line {
    stroke-dashoffset: 200;
}

g.out-groups {
    opacity: 0;
}



